* {
    box-sizing: border-box;
}
a:link { text-decoration: none; }

a:visited { text-decoration: teal; }

a:hover { text-decoration: teal; }

a:active { text-decoration: none; }
body {
    background-color:rgb(236, 186, 157);
    height: fit-content;
    margin: 2;  
    display: flex;
    font-family: 'Inter', sans-serif;

  }
nav {
    display: flex;
    background-color: rgb(231, 172, 184);
    width: 100%;
    height: 100px;
    left: 0px;
    top: 0px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    align-items: center;
    height: 90px;
    padding: 20px 25px;
    
    

}
.nav--logo { 
    
    height: 95px;
   
    
}
.nav--logo-text {

    font-weight: 600;
    color: teal;
    margin-right: auto;
    
}

.nav--button-text {
    
    font-weight: bold;
    color:black;
    border-radius: 50%;
    
    height: 50px;
    width: 90px;
    margin: 10px; 
    font-size: large;
    font-family: 'Inter', sans-serif;
    cursor: pointer;
    border-style: solid;
    
}
.nav--button-text:hover {
    background-color: coral;
}

.contacts {
    display: flex;
    justify-content:left;
    flex-wrap: wrap;
    gap: 40px;
}

.vdo-card {
    display:flex;
    flex-basis: 100%;
    box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    flex-direction: column;
    padding: 13px;
    padding-bottom: 20px;
    background-color: white;
}

.vdo-img {
    width: 100%;
    height: 75%;
    border-radius: 9px;
    margin-bottom: 9px;
}

.contact-card > h3 {
    font-weight: 700;
    font-size: 18px;
}

.info-group {
    display: flex;
    align-items: center;
}

.info-group > img {
    height: 11px;
    margin-right: 8px;
}

.info-group > p {
    margin-block: 3px;
    font-size: 12px;
    color: #2B283A;
}
.info-group > a {
    margin-block: 3px;
    font-size: 12px;
    color: #2B283A;

}
.footer {   
    height: auto;
    align-items: center;
    width: 100%;
    background-color: #898cad;
    border-radius: 5px;
    color: white;
    text-align: center;
    position: absolute;
    margin-top: 20px;
}

.vdo-list {
    display: flex;
    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;
    
}
.main--section {
    
    background-color: aqua;
    margin-top: 10px;
    padding: 10px;
    border-radius: 5px;
    align-content: center;
    
}
.main-button {
    width: 100%;
    background-color:green;
    padding: 1rem;
    border: 2px solid white;
    border-radius: 50px;
    cursor: pointer;
    font-size: larger;
    font-family: 'Karla', sans-serif;
    margin-bottom: 20px;
    font-weight: bolder;
}

.main-button:hover {
    background-color: #dbc067;
    color: #2C5E2E;
}

.main-button:focus {
    outline: 0;
}
.div--url-experiment {
    display: flex;
    
}
.div-button {
    background-color: rgb(106, 190, 36);
    border-style: double;
    height: 30px;
    width: 50px;
    border-radius: 50%;
    margin-right: auto;
    font-weight: bolder;
    justify-content: center;
    align-items: center;
    color: #262626;
    cursor: pointer;

}


.state {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.state--title {
    text-align: center;
    margin-bottom: 40px;
}

.state--value {
    background-color: white;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #262626;
    cursor: pointer;
}
.card-div {
    margin:auto;
    display: flex;
    width: 310px;
    margin-top: 10px;
    background-color:antiquewhite;
    border-radius: 5px;
    
}
.card {
    
    border: 1px solid lightgray;
    border-radius: 10px;
    height: 450px;
    box-shadow: 5px 5px 5px 1px rgba(120,120,120,0.44);
    -webkit-box-shadow: 5px 5px 5px 1px rgba(120,120,120,0.44);
    -moz-box-shadow: 5px 5px 5px 1px rgba(120,120,120,0.44);
}

.card--image {
    width: 100%;
    padding: 10%;
    padding-bottom: 0;
}

.card--name {
    margin-block: 13px;
    color: #333333;
}

.card--info {
    padding: 10px;
}

.card--favorite {
    width: 25px;
    cursor: pointer;
}

.card--contact {
    font-size: 0.75rem;
    color: gray;
    margin-block: 7px;
}
.color-div {
    
    margin: auto;
    width:310px;
    height: 50px;
    text-align: center;
    border-radius: 5px;
    margin-top: 10px;
}
.color-div-button {
    text-align: center;
    font-weight: 600;
    background-color: brown;
    border-radius: 5px;
    height: 30px;
    
    
}
